<template>
   <div class="container">
       <div class="content text-center">
           <h2>Add Post</h2>
       </div>
       <form @submit.prevent="savePost">
            <b-field label="ID">
                <b-input v-model="post_id"></b-input>
            </b-field>
            <b-field label="Title">
                <b-input v-model="title"></b-input>
            </b-field>
            <b-field label="Content">
                <b-input v-model="content" type="textarea"></b-input>
            </b-field>     
            <button type="submit" class="button is-primary">
                Send message
            </button>       
        </form>
   </div>
</template>

<script>
    import db from './../firebase/firebaseInit'
    export default {
        data(){
            return {
                post_id: null,
                title: null,
                content: null,
                created_at: ""
            }
        },
        methods: {
            savePost(){
                db.collection('/blogs/drB2C5hyY7HhmVNdAjG8/news').add({
                    post_id: this.post_id,
                    title: this.title,
                    content: this.content,
                    like: 0,
                    created_at: new Date()
                })
                .then(docref => {
                    this.$router.push('/')
                    this.$store.dispatch('getAllPosts')
                })
                .catch(error => console.log(err))
            }
        }
    }
</script>

<style>
    form {
        max-width: 500px;
        margin: 20px auto;
    }
</style>

